import React from "react";
import { makeStyles } from "@material-ui/core/styles";
import Paper from "@material-ui/core/Paper";
import Tabs from "@material-ui/core/Tabs";
import Tab from "@material-ui/core/Tab";
import Button from "@material-ui/core/Button";
import Menu from "@material-ui/core/Menu";
import MenuItem from "@material-ui/core/MenuItem";
import { Link } from "gatsby";

const useStyles = makeStyles({
	root: {
		flexGrow: 0,
	},
	menu: {
		"@media (max-width:1100px)": {
			display: "none",
		},
		flexGrow: 0,
	},
	tab: {
		padding: "20px",
		minWidth: 0,
	},
	link: {
		color: "black",
		fontFamily: "WalkwayUltraBold",
		"&:hover": {
			textDecoration: "none",
		},
	},
	desktopnav: {
		display: "flex",

		"@media (max-width:1101px)": {
			display: "none",
		},
	},
});

const DiveWithUsButton = () => {
	const classes = useStyles();
	const [anchorEl, setAnchorEl] = React.useState(null);
	const open = Boolean(anchorEl);
	const handleClick = (event) => {
		setAnchorEl(event.currentTarget);
	};
	const handleClose = () => {
		setAnchorEl(null);
	};

	return (
		<div>
			<Button
				id="basic-button"
				aria-controls="basic-menu"
				aria-haspopup="true"
				aria-expanded={open ? "true" : undefined}
				onClick={handleClick}
				className={classes.link}
			>
				Dive With Us
			</Button>
			<Menu
				id="basic-menu"
				anchorEl={anchorEl}
				open={open}
				onClose={handleClose}
				MenuListProps={{
					"aria-labelledby": "basic-button",
				}}
				anchorOrigin={{
					vertical: "bottom",
					horizontal: "left",
				}}
				transformOrigin={{
					vertical: "bottom",
					horizontal: "left",
				}}
			>
				<MenuItem onClick={handleClose}>
					<Link to="/discover-scuba-diving" className={classes.link}>
						Discover Scuba
					</Link>
				</MenuItem>
				<MenuItem onClick={handleClose}>
					<Link to="/padi-open-water" className={classes.link}>
						PADI Open Water
					</Link>
				</MenuItem>
				<MenuItem onClick={handleClose}>
					<Link to="/advanced-open-water" className={classes.link}>
						PADI Advanced
					</Link>
				</MenuItem>
			</Menu>
		</div>
	);
};

