import React from "react";
import { useEffect, useState } from "react";
import useScrollListener from "../utils/useScrollListener";
import { makeStyles } from "@material-ui/core/styles";
import AppBar from "@material-ui/core/AppBar";
import Toolbar from "@material-ui/core/Toolbar";
import MobileNav from "../components/MobileNav";
import Menu from "../components/Menu";
import logo from "./site-logo.svg"; // Tell Webpack this JS file uses this image
import { Link } from "gatsby";
import FacebookIcon from "@material-ui/icons/Facebook";
import InstagramIcon from "@material-ui/icons/Instagram";
import YouTubeIcon from "@material-ui/icons/YouTube";

const useStyles = makeStyles((theme) => ({
	root: {
		flexGrow: 1,
	},
	menuButton: {
		alignSelf: "flex-end",
		justifySelf: "flex-end",
	},
	title: {
		flexGrow: 1,
		textAlign: "center",
	},
	logo: {
		maxWidth: "220px",
		marginBottom: 0,
		"@media (min-width:1101px)": {
			maxWidth: "320px",
			justifySelf: "center",
			order: 2,
		},
	},
	container: {
		width: "100vw",
		display: "flex",
		justifyContent: "space-between",
		alignItems: "center",
		"@media (min-width:1101px)": {
			justifyContent: "space-evenly",
		},
	},
	link: {
		maxHeight: "20px",
	},
	menu: {
		"@media (max-width:1101px)": {
			display: "none",
		},
	},
	socialbar: {
		display: "flex",
		justifyContent: "center",
		width: "33.3vw",
		"@media (max-width:1101px)": {
			display: "none",
		},
	},
	socialButtonsWrapper: {
		display: "flex",
		justifyContent: "space-evenly",
		alignItems: "space-evenly",
		width: "300px",
	},
}));

export default function ButtonAppBar() {
	const classes = useStyles();

	const [navClassList, setNavClassList] = useState([]);
	const scroll = useScrollListener();
	// update classList of nav on scroll
	useEffect(() => {
		const _classList = [];

		if (scroll.y > 150 && scroll.y - scroll.lastY > 0)
			_classList.push("nav-bar--hidden");

		setNavClassList(_classList);
	}, [scroll.y, scroll.lastY]);

	return (
		<>
			<style
				dangerouslySetInnerHTML={{
					__html: `nav {
      flex-grow: 1;
      transition: opacity 250ms ease-in-out;
    }

    nav.nav-bar--hidden {
      opacity: 0;
    }`,
				}}
			/>

			<nav className={navClassList.join(" ")}>
				<AppBar position="fixed">
					<Toolbar>
						<div className={classes.container}>
							<div className={classes.socialbar}>
								<div className={classes.socialButtonsWrapper}>
									<FacebookIcon color="secondary" />
									<InstagramIcon color="secondary" />
									<YouTubeIcon color="secondary" />
								</div>
							</div>
							<Link to="/">
								<img
									className={classes.logo}
									src={logo}
									alt="Fifty Six"
									sizes="(max-width: 500px) 100vw, (max-width: 900px) 50vw, 800px"
								/>
							</Link>
							<MobileNav className={classes.menuButton} />
							<div className={classes.menu}>
								<Menu />
							</div>
						</div>
					</Toolbar>
				</AppBar>
			</nav>
		</>
	);
}
